body {
    // overflow: hidden
    // height: 100vh
    background-color: #000;
    #anime_box {
        padding: 5px 24px;
        #card_content {
            width: 100%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            #card {
                width: 121px;
                height: 121px;
                margin: 6px;
                padding: 25px;
                border: 2px #FFF solid;
                .cssSelector {
                    margin: -25px 50px;
                    width: 20px;
                    height: 20px;
                    background-color: #fff; }
                .DomNodeSelector {
                    width: 20px;
                    height: 20px;
                    margin: 15px 50px;
                    background-color: #000; }
                .cssTransforms {
                    margin: 50px 50px;
                    width: 20px;
                    height: 20px;
                    border: 2px #FFF solid; }
                .cssProrerties {
                    margin: 10px 10px;
                    width: 100px;
                    height: 100px;
                    border: 1px #FFF solid;
                    border-radius: 50%; }
                .cssProrerties1 {
                    margin: 22px 25px;
                    width: 50px;
                    height: 50px;
                    border: 1px #FFF solid;
                    border-radius: 50%; }
                .div3 {
                    margin: 45px 45px;
                    width: 30px;
                    height: 30px;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: center;
                    .div4 {
                        width: 4px;
                        height: 4px;
                        margin: 1px 1px;
                        background-color: #FFF; } }
                .SpecificPropParamsL {
                    margin: 50px -25px;
                    width: 20px;
                    height: 20px;
                    background-color: #000; }
                .polymorph {
                    fill: none;
                    stroke: #FFF;
                    stroke-miterlimit: 10;
                    stroke-width: 2; }
                .FunctionBasedValues3 {
                    width: 45px;
                    height: 45px;
                    margin: 40px 40px;
                    background-color: #FFF; }
                .FunctionBasedValues2 {
                    width: 20px;
                    height: 20px;
                    margin: -70px 50px;
                    background-color: #FFF; }
                .FunctionBasedValues1 {
                    width: 10px;
                    height: 10px;
                    margin: 55px 55px;
                    background-color: #FFF; }
                .StepEasing {
                    width: 20px;
                    height: 20px;
                    background-color: #FFF;
                    margin: -10px; } } } } }
